<template>
    <div class="box">
        <div class="box-container">
            <div class="title">雅韶先锋后台管理</div>
            <div class="inp">
                <!-- <div class="inp-title">账户: </div> -->
                <el-input class="inp-el" prefix-icon="el-icon-user" v-model="form.mobile" placeholder="请输入账号"></el-input>
            </div>
            <div class="inp">
                <!-- <div class="inp-title">密码: </div> -->
                <el-input class="inp-el" prefix-icon="el-icon-edit" type="password" v-model="form.password" placeholder="请输入密码"></el-input>
            </div>
            <div class="login-inp-box">
                <el-button @click="loginBtn" class="login-inp" type="primary">登陆</el-button>
                <div class="register" @click="register">
                    注册
                </div>
            </div>
        </div>
        <div class="bottom-box">
            Copyright 2022 yashaoxf.com All Rights Reserved. 雅韶先锋 版权所有
            <br>
            <br>
            <div class="copyright-btn" @click="goCopyRight">
                粤ICP备2022139784号-1
            </div>
        </div>
    </div>
</template>

<script>
import { loginUser } from '@/api/login';
export default {
    data () {
        return {
            form: {
                mobile: '',
                password: '',
                loginType: '账户密码'
            }
        };
    },
    methods: {
        loginBtn () {
            loginUser(this.form).then(res => {
                if (res.data.code == 0) {
                    this.$message.success(res.data.message);
                    this.$store.commit('updateUserData',
                        JSON.stringify({
                            accessToken: res.data.data.accessToken,
                            userType: 'admin'
                        })
                    );
                    this.$store.commit('setUserName', this.form.mobile);
                    // 登陆成功后获取一次区域地址
                    setTimeout(() => {
                        this.$router.push({ path: '/welcome' });
                    }, 2000);
                } else {
                    this.$message.error(`${res.data.message},${res.data.detail}`);
                }
            });
        },
        register () {
            this.$router.push({
                path: '/register'
            });
        },
        goCopyRight () {
            window.open('https://beian.miit.gov.cn');
        },
    }
}
</script>

<style scoped>
.box {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.box-container {
    width: 400px;
    height: 300px;
    box-shadow: 1px 2px 4px #ccc;
    border-radius: 4px;
}

.title {
    font-size: 16px;
    font-weight: bold;
    width: 100%;
    text-align: center;
    padding: 20px 0;
}

.inp {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.inp-title {
    width: 80px;
    text-align: right;
}

.inp-el {
    flex: 1;
    margin-right: 20px;
    margin-left: 20px;
}
.login-inp-box {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
}
.login-inp {
    flex: 1;
    margin-right: 20px;
    margin-left: 20px;
}
.register {
    width: 100%;
    text-align: center;
    padding-top: 30px;
    cursor: pointer;
    font-size: 12px;
}
.bottom-box {
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding-bottom: 20px;
}
.copyright-btn {
    cursor: pointer;
}
</style>